import loadEcharts from "./load-echarts";

module.exports = [
    {
        id: 'jupyterlab_cfps_preload',
        autoStart: true,
        activate: function (app) {
            console.log(
                'JupyterLab extension jupyterlab_cfps_preload is activated!'
            );
            let counter = 5;
            while (counter--) {
                if (loadEcharts()) break;
            }
            console.log(app.commands);
        }
    }
];
